.header_wrapper{
    z-index:100;
    margin: 0;
    top:0;
    padding: 20px 50px;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: absolute;
    width: 100%;
    height: 103px;

    background: linear-gradient(90.53deg, rgba(0, 0, 0, 0.75) 0.45%, rgba(102, 80, 165, 0.75) 105.51%);
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.25);
}
.logo{
    font-family: 'Griffy', cursive;
    font-style: normal;
    font-weight: 400;
    font-size: 36px;
    line-height: 49px;
    color: #FFC907;
    text-decoration: none;
    cursor: pointer;
    width: 180px;
    height: 49px;
}
.menu_item{
    /* width:100%; */
    /* display: flex; */
    text-decoration: none;
    cursor: pointer;
    width: 53px;
    height: 24px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    color: #FFFFFF;
}